/*–°–º–∞—Ä—Ç—Ñ–æ–Ω—ã: –æ—Ç 320px –¥–æ 480px. ======================================================= */
/* 1. –°–∫—Ä—ã–≤–∞–µ–º –æ—Å–Ω–æ–≤–Ω—É—é –Ω–∞–≤–∏–≥–∞—Ü–∏—é –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é) */
.navbar {
  display: none; /* –°–∫—Ä—ã–≤–∞–µ–º –¥–µ—Å–∫—Ç–æ–ø–Ω—É—é –Ω–∞–≤–∏–≥–∞—Ü–∏—é */
}
/* 3. –°—Ç–∏–ª–∏ –¥–ª—è –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é (–≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∞—è —Ä–∞—Å–∫–ª–∞–¥–∫–∞) */
.navbar_v {
  /* –≠—Ç–æ —Å–∞–º–æ –º–µ–Ω—é, –∫–æ—Ç–æ—Ä–æ–µ –±—É–¥–µ—Ç –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞—Ç—å—Å—è */
  position: absolute;
  top: 100%; /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º –ø–æ–¥ —Ö–µ–¥–µ—Ä–æ–º */
  left: 0;
  width: 100%;
  background-color: #f8f9fa; /* –§–æ–Ω –∫–∞–∫ —É —Ö–µ–¥–µ—Ä–∞ */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 100;
  padding: 10px 0;
}

.navbar_v ul {
  /* –í–∞—à–∏ —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–µ —Å—Ç–∏–ª–∏ –¥–ª—è –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–≥–æ —Å–ø–∏—Å–∫–∞ */
  display: block;
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar_v li {
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}
.navbar_v li:last-child {
  border-bottom: none;
}
.navbar_v a {
  text-decoration: none;
  color: #333;
  display: block; /* –ó–∞–Ω–∏–º–∞–µ—Ç –≤—Å—é —à–∏—Ä–∏–Ω—É —ç–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫–∞ */
}

/* –ü–ª–∞–Ω—à–µ—Ç—ã: –æ—Ç 768px –¥–æ 1024px. ======================================================= */
@media (min-width: 480px) {
  .container {
    max-width: 460px;
  }
}
@media (min-width: 768px) {
  .navbar-v {
    display: none;
  }
  .navbar {
    display: block;
  }

  .navbar ul {
    display: flex; /* –í–æ–∑–≤—Ä–∞—â–∞–µ–º –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—É—é —Ä–∞—Å–∫–ª–∞–¥–∫—É –¥–ª—è –¥–µ—Å–∫—Ç–æ–ø–∞ */
    justify-content: center;
    gap: 20px;
    list-style: none;
    padding: 0;
    margin: 0;
  }
}
/* –ù–æ—É—Ç–±—É–∫–∏ –∏ –ü–ö: –æ—Ç 1024px –∏ –≤—ã—à–µ. –î–ª—è –±–æ–ª—å—à–µ–π –¥–µ—Ç–∞–ª–∏–∑–∞—Ü–∏–∏ –∏—Å–ø–æ–ª—å–∑—É—é—Ç 1280px======================================================= */
@media (min-width: 1024px) {
  .container {
    max-width: 1000px;
  }
}
/* –ù–∞—Å—Ç–æ–ª—å–Ω—ã–µ –ü–ö: –æ—Ç 1280px======================================================= */
@media (min-width: 1280px) {
  .container {
    max-width: 1220px;
  }
}
/* ======================================================= */
@media (min-width: 1560px) {
  .container {
    max-width: 1500px;
  }
}
/* ======================================================= */
@media (min-width: 1920px) {
  .container {
    max-width: 1860px;
  }
}
