/* hero.module.css */
/*
.hero {
    position: relative; /* –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –ø–æ—Ç–æ–º–∫–æ–≤ 
    min-height: 400px; –£–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ —É .hero –µ—Å—Ç—å –≤—ã—Å–æ—Ç–∞ 
    overflow: hidden;
    padding: 100px 0;
  text-align: center;
}
*/
.hero__title {
  font-size: 2.5rem;
  margin-bottom: 20px;
    color:whitesmoke;
}


.hero__btn {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
}

.hero__btn:hover {
  background-color: #0056b3;
}
.hero__subtitle {
  font-size: 1.2rem;
  color: #555;
  margin-top: 15px;
}


.hero {
    /* 1. –ö–û–ù–¢–ï–ô–ù–ï–† HERO */
    position: relative; /* –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ: –∫–æ–Ω—Ç–µ–∫—Å—Ç –¥–ª—è –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è */
    overflow: hidden; /* –û–±—Ä–µ–∑–∫–∞ –≤—Å–µ–≥–æ, —á—Ç–æ –≤—ã—Ö–æ–¥–∏—Ç –∑–∞ —Ä–∞–º–∫–∏ */
}

/* 2. –°–¢–ò–õ–¨ –î–õ–Ø <picture> (–§–û–ù) */
.hero__background {
    position: absolute;
    /* –ì–ê–†–ê–ù–¢–ò–Ø –†–ê–°–¢–Ø–ì–ò–í–ê–ù–ò–Ø –ù–ê –í–ï–°–¨ –ë–õ–û–ö: */
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1; /* –ü–æ–º–µ—â–∞–µ–º –ø–æ–¥ –∫–æ–Ω—Ç–µ–Ω—Ç–æ–º */
}

/* 3. –°–¢–ò–õ–¨ –î–õ–Ø <img> –í–ù–£–¢–†–ò <picture> */
.hero__image { 
    width: 100%;
    height: 100%;
    object-fit: cover; /* –≠—Ñ—Ñ–µ–∫—Ç background-size: cover */
    /* –î–æ–±–∞–≤–ª—è–µ–º –ø–ª–∞–≤–Ω–æ—Å—Ç—å –¥–ª—è –ª—É—á—à–µ–≥–æ UX */
    transition: opacity 0.3s ease;
}

/* 4. –°–¢–ò–õ–¨ –î–õ–Ø –ö–û–ù–¢–ï–ù–¢–ê (–ó–∞–≥–æ–ª–æ–≤–æ–∫, –∫–Ω–æ–ø–∫–∞) */
.hero__content {
    position: relative; 
    z-index: 2; /* –ü–æ–º–µ—â–∞–µ–º –∫–æ–Ω—Ç–µ–Ω—Ç –ø–æ–≤–µ—Ä—Ö —Ñ–æ–Ω–∞ */
    /* –î–æ–±–∞–≤–∏–º Flexbox –¥–ª—è –ª—É—á—à–µ–≥–æ —Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏—è, –µ—Å–ª–∏ –ø–æ—Ç—Ä–µ–±—É–µ—Ç—Å—è, 
       –Ω–æ –ø–æ–∫–∞ –æ—Å—Ç–∞–≤–∏–º –∫–∞–∫ –±—ã–ª–æ, –∏—Å–ø–æ–ª—å–∑—É—è padding —Ä–æ–¥–∏—Ç–µ–ª—è */
    text-align: center; 
    padding: 130px 0; /* –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –æ—Ç—Å—Ç—É–ø –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
}



