/*
 * –°—Ç–∏–ª–∏ –¥–ª—è TeamCard
 */
.teamCard {
    /* –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ, —á—Ç–æ–±—ã –∫–∞—Ä—Ç–æ—á–∫–∞ –≤—ã–≥–ª—è–¥–µ–ª–∞ –∫–∞–∫ –≤ –≤–∞—à–µ–º HTML */
    background-color: #ffffff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
        0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
}

.teamCard__img {
    display: block;
    width: 100%;
    height: auto;
}

.teamCard__footer {
    padding: 30px 0;
}

.teamCard__text {
    font-size: 16px;
    line-height: 1.18;
    color: #757575;
    /* primary-txt-color */
    margin: 0 0 16px 0;
}

.teamCard__name {
    display: block;
    font-weight: 500;
    font-size: 16px;
    color: #212121;
    /* secondary-txt-color */
    margin-bottom: 10px;
}

/*
 * –°—Ç–∏–ª–∏ –¥–ª—è –°–æ—Ü–∏–∞–ª—å–Ω—ã—Ö —Å—Å—ã–ª–æ–∫
 */
.teamCard__socPos {
    /* –≠—Ç–æ—Ç div –æ–±–µ—Ä—Ç–∫–∞ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω –¥–ª—è –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –≤ –≤–∞—à–µ–º HTML */
    padding-bottom: 30px;
}

.teamCard__social,
.social {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin: 0;
    gap: 10px;
    /* –ü—Ä–æ–º–µ–∂—É—Ç–æ–∫ –º–µ–∂–¥—É –∏–∫–æ–Ω–∫–∞–º–∏ */
}

.social__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    /*fill: #AFB1B8;*/
    background-color: #ffffff;
    border: 1px solid #2196F3;
    fill: #2196F3;
   

    transition: fill 250ms ease-in-out, background-color 250ms ease-in-out;
}

@media screen and (min-width: 1200px) {
    .social__link {
        fill: #AFB1B8;
        border: 1px solid #AFB1B8;
        transition: fill 250ms ease-in-out, background-color 250ms ease-in-out;
    }


    /* –ò–ó–ú–ï–ù–ï–ù–ò–ï 1: –°—Ç–∏–ª–∏ –•–æ–≤–µ—Ä–∞ —Ç–æ–ª—å–∫–æ –¥–ª—è –î–µ—Å–∫—Ç–æ–ø–∞ */
    .social__link:hover,
    .social__link:focus {
        /* –ü—Ä–∏ —Ö–æ–≤–µ—Ä–µ: –§–æ–Ω —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è –≥–æ–ª—É–±—ã–º, –∏–∫–æ–Ω–∫–∞ –±–µ–ª–æ–π */
        background-color: #2196F3;
        fill: #ffffff;
        border-color: #2196F3;
        /* –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –æ–±–≤–æ–¥–∫–∞ –æ—Å—Ç–∞–µ—Ç—Å—è –≥–æ–ª—É–±–æ–π (–∏–ª–∏ —Ç–æ–≥–æ –∂–µ —Ü–≤–µ—Ç–∞, —á—Ç–æ –∏ —Ñ–æ–Ω) */
    }
}