.notFound {
    display: flex;
    flex-direction: column;
    align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ—Ç —ç–ª–µ–º–µ–Ω—Ç—ã –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
    justify-content: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ—Ç —ç–ª–µ–º–µ–Ω—Ç—ã –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ (–µ—Å–ª–∏ —Ä–æ–¥–∏—Ç–µ–ª—å –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –≤—ã—Å–æ–∫–∏–π) */
    text-align: center;
    padding: 50px 20px;
    /* –£—Å—Ç–∞–Ω–æ–≤–∏—Ç–µ –º–∏–Ω–∏–º–∞–ª—å–Ω—É—é –≤—ã—Å–æ—Ç—É, —á—Ç–æ–±—ã –∫–æ–Ω—Ç–µ–Ω—Ç –Ω–µ –±—ã–ª —Å–∂–∞—Ç */
    min-height: 50vh; 
}

/* –ö–æ–¥ –æ—à–∏–±–∫–∏ */
.errorCode {
    font-size: 8rem; /* –û—á–µ–Ω—å –±–æ–ª—å—à–æ–π —Ä–∞–∑–º–µ—Ä */
    font-weight: 900;
    color: var(--color-primary, #e74c3c); /* –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ –≤–∞—à –æ—Å–Ω–æ–≤–Ω–æ–π —Ü–≤–µ—Ç –∏–ª–∏ –∫—Ä–∞—Å–Ω—ã–π */
    margin: 0;
    line-height: 1;
}

/* –û—Å–Ω–æ–≤–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ */
.message {
    font-size: 2rem;
    margin-top: 20px;
    margin-bottom: 10px;
    color: var(--color-text-main, #333);
}

/* –ü–æ–¥—Ä–æ–±–Ω–æ–µ –æ–ø–∏—Å–∞–Ω–∏–µ */
.description {
    font-size: 1.1rem;
    color: var(--color-text-secondary, #666);
    margin-bottom: 30px;
    max-width: 400px;
}

/* –°—Å—ã–ª–∫–∞ –Ω–∞ –≥–ª–∞–≤–Ω—É—é */
.homeLink {
    display: inline-block;
    padding: 12px 25px;
    background-color: var(--color-accent, #3498db);
    color: #ffffff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: 600;
    transition: background-color 0.3s ease;
    border: none;
    cursor: pointer;
}

.homeLink:hover {
    background-color: var(--color-accent-dark, #2980b9);
}

