/* s.module.css - Mobile First Approach */

/* ======================================= */
/* 1. –ë–ê–ó–û–í–´–ï –°–¢–ò–õ–ò (MOBILE / –î–û 768px)  */
/* ======================================= */

.gallery {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    gap: 30px;
}

.item {
    /* 1 —ç–ª–µ–º–µ–Ω—Ç –≤ —Ä—è–¥ (–ú–æ–±–∏–ª—å–Ω—ã–π) */
    flex-basis: 100%;
    position: relative;
    overflow: hidden;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.item img {
    display: block;
    width: 100%;
    height: auto;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –ø–æ–¥–ø–∏—Å–∏ (s.bgTitle) - –í–°–ï–ì–î–ê –í–ò–î–ò–ú –Ω–∞ –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–∞—Ö */
.bgTitle {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: auto;
    /* –ê–¥–∞–ø—Ç–∏–≤–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
    padding: 15px;
    /* –£–º–µ–Ω—å—à–µ–Ω–Ω—ã–µ –æ—Ç—Å—Ç—É–ø—ã –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö */

    background-color: rgba(47, 48, 58, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;

    /* –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ: –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö opacity: 1 –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é! */
    opacity: 1;
}

.imgTitle {
    color: #ffffff;
    font-weight: 700;
    font-size: 16px;
    /* –ß—É—Ç—å –º–µ–Ω—å—à–µ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö */
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
}

/* ======================================= */
/* 2. –ü–õ–ê–ù–®–ï–¢–´ (768px –∏ –≤—ã—à–µ)           */
/* ======================================= *//*
@media screen and (min-width: 768px) {
    .item {
       
        flex-basis: calc((100% - 30px) / 2);
    }



    .imgTitle {
        font-size: 18px;
        
    }
}*/

/* ======================================= */
/* 2. –ü–õ–ê–ù–®–ï–¢–´ (768px –¥–æ 1199px) - –°–õ–ê–ô–°  */
/* ======================================= */
@media screen and (min-width: 768px) and (max-width: 1199px) {

    /* 1. –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –≥–∞–ª–µ—Ä–µ–∏ (ul.gallery) */
    .gallery {
        /* –ì–õ–ê–í–ù–û–ï: –û–¢–ö–õ–Æ–ß–ê–ï–ú –ø–µ—Ä–µ–Ω–æ—Å —Å—Ç—Ä–æ–∫ */
        flex-wrap: nowrap;

        /* –ì–õ–ê–í–ù–û–ï: –î–û–ë–ê–í–õ–Ø–ï–ú –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—É—é –ø—Ä–æ–∫—Ä—É—Ç–∫—É */
        overflow-x: auto;
        overflow-y: hidden;
    }

    /* 2. –≠–ª–µ–º–µ–Ω—Ç —Å–ø–∏—Å–∫–∞ (li.item) */
    .item {
        /* –î–µ–ª–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã –ù–ï —Å–∂–∏–º–∞–µ–º—ã–º–∏ –∏ –ù–ï —Ä–∞—Å—Ç—è–∂–∏–º—ã–º–∏ */
        flex-shrink: 0;
        flex-grow: 0;

        /* –®–∏—Ä–∏–Ω–∞ –¥–ª—è 2—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤ —Ä—è–¥—É (–¥–æ–ª–∂–Ω–æ —É–º–µ—â–∞—Ç—å—Å—è 2 —ç–ª–µ–º–µ–Ω—Ç–∞ + –Ω–µ–º–Ω–æ–≥–æ –º–µ—Å—Ç–∞ –¥–ª—è —Å–ª–µ–¥—É—é—â–µ–≥–æ) */
        flex-basis: calc((100% - 30px) / 2);
    }

    /* –°—Ç–∏–ª–∏ –¥–ª—è –ø–æ–¥–ø–∏—Å–∏ (–ù–∞ –ø–ª–∞–Ω—à–µ—Ç–∞—Ö –æ–Ω–∞ –≤–∏–¥–∏–º–∞ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é) */
    .bgTitle {
        opacity: 1;
        /* –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤–∏–¥–∏–º–æ—Å—Ç—å */
    }

    .imgTitle {
        font-size: 18px;
    }

    /* –£–±–µ–∂–¥–∞–µ–º—Å—è, —á—Ç–æ Hover-—ç—Ñ—Ñ–µ–∫—Ç—ã –Ω–µ –∞–∫—Ç–∏–≤–Ω—ã */
    .item:hover .bgTitle {
        opacity: 1;
    }

    .item:hover {
        box-shadow: none;
    }
}

/* ======================================= */
/* 3. –î–ï–°–ö–¢–û–ü (1200px –∏ –≤—ã—à–µ)           */
/* ======================================= */
@media screen and (min-width: 1200px) {
    .item {
        /* 3 —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ —Ä—è–¥ */
        flex-basis: calc((100% - 60px) / 3);
    }

    /* –°–ö–†–´–í–ê–ù–ò–ï –ü–û–î–ü–ò–°–ò –ü–û –£–ú–û–õ–ß–ê–ù–ò–Æ (–¢–æ–ª—å–∫–æ –Ω–∞ –¥–µ—Å–∫—Ç–æ–ø–µ!) */
    .bgTitle {
        opacity: 0;
        height: 70px;
        /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ –¥–ª—è –¥–µ—Å–∫—Ç–æ–ø–∞ */
        padding: 24px;
        /* –í–æ—Å—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã */
        transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    /* –ê–∫—Ç–∏–≤–∞—Ü–∏—è HOVER-—ç—Ñ—Ñ–µ–∫—Ç–∞ (–¢–æ–ª—å–∫–æ –Ω–∞ –¥–µ—Å–∫—Ç–æ–ø–µ!) */
    .item:hover .bgTitle {
        opacity: 1;
        /* –ü–æ–∫–∞–∑—ã–≤–∞–µ—Ç –ø–æ–¥–ø–∏—Å—å –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
    }

    .item:hover {
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
            0px 4px 4px rgba(0, 0, 0, 0.06),
            1px 4px 6px rgba(0, 0, 0, 0.16);
    }
}